body {
  padding: 10px;
  font-family: sans-serif;
  background-color: lightgrey;
}

h1 {
  text-align: center;
  font-size: 4vw;
  font-weight: 100;
  margin: 0.2em;
  color: #373737;
}

h3 {
  text-align: center;
}

p {
  text-align: center;
}

a {
  color: white;
}

.appContainer {
  margin: auto;
  width: 1000px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 110px 110px 110px;
  padding: 2px;
}

.sound-card {
  width: 200px;
  background-color: #3EAAFE;
  color: #fff;
}

div.sound-card:nth-of-type(odd) {
  background-color: #000;
  color: #fff;
}

.image-container {
  max-width: 40px;
  float: left;
  padding-left: 10px;
}

.count {
  height: 40px;
  padding-left: 60px;
  line-height: 45px;
}

@media only screen and (max-width: 899px) {
  .appContainer {
    margin: auto;
    width: 600px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 110px 110px 110px;
    padding: 2px;
  }
}